.sidebar-container{
  width: 150px;
  min-height: 100vh;
  background-color: #1D3668;
  color:white;
  display: inline-block;
  margin: 0;
  padding: 0;
  position: fixed;

}

 .sidebar {
   width: 150px;
   padding-top:25px;
   padding-left: 15px;
 }

 .sidebar-link:hover{
   border-right: 5px solid #343434;
   background-color: #181f50;

 }
 .sidebar-link{
   height:50px;
   padding:10px;

 }


